<template>
    <div class="set">
        <div class="contentList">
            <div class="listItem" v-for="(item, index) in lists" :key="index" v-on:click="go(index)">
              <span><i class="iconfont" :class="item.con"></i></span>
              <span>{{item.text}}</span>
              <span><i class="iconfont icon-dayu"></i></span>
            </div>
          </div>
    </div>
</template>
<script>
export default {
    data(){
      return {
        lists: [{
                  con: 'icon-pingjia-copy',
                  text: '账号设置',
                },
                {
                  con: 'icon-bianji',
                  text: '修改密码',
                },
                {
                  con: 'icon-yaoqing',
                  text: '推送设置',
                },
                {
                  con: 'icon-yaoqinghaoyou',
                  text: '清理缓存',
                },
                {
                  con: 'icon-shoucang',
                  text: '意见反馈',
                },
                {
                  con: 'icon-weibiaoti24',
                  text: '评分',
                },
                {
                  con: 'icon-weibiaoti24',
                  text: '关于',
                },
        ]
      }
    },
    methods:{
      go(index){
        switch(index){
          case 0: 
            this.$router.push('account');
            break;
          default:
            break;
        }
      }
    }
}
</script>
<style lang="less" scoped>
.set{
    .contentList{
        padding: 0 15px;
        .listItem{
          height: 50px;
          border-bottom: 1px solid #eeeeee;
          display: flex;
          align-items: center;
          &:active{
            background:#f1f1f1;
          }
          & > span:nth-child(1){
            display: inline-block;
            width: 20px;
            height: 20px;
            // background: #999999;
            padding-left: 4px;
            & > i{
              font-size: 18px;
            }
          }
          & > span:nth-child(2){
              flex: 1;
              margin-left: 8px;
          }
          & > span:nth-child(3){
            display: inline-block;
            width: 20px;
            height: 20px;
            // background: #999999;
            & > i{
              color: #999999;
              font-size: 18px;
            }
          }
        }
      }
}
</style>



