<template>
  <div id="issue" class="issue">
    <!-- <headerBar/> -->
    <!-- <div class="header">
      发布心情
    </div> -->
    <div class="suggestions-content suggestions-item">
        <textarea name="suggestions-text" id="textarea" cols="30" rows="10" maxlength="300" v-on:input="inputText()" v-on:keyup="write()" v-on:compositionstart="importStart()"
            v-on:compositionend="importEnd()" v-model="textContent" placeholder="发布你的心情吧。"></textarea>
        <p class="counterNum">{{conterNum}}/300</p>
    </div>

    <div class="suggestions-pic suggestions-item">
        <h2>上传图片
            <span class="little-text">（选填）</span>
            <span class="pic-num">{{picNum}}/9</span>
        </h2>
        <ul class="get-pic">
            <li v-for="(item,index) in upImgUrl" :key="index">
                <img :src="item" alt="">
                <span class="cha" v-on:click.stop="closeImg(index)">×</span>
            </li>
            <li v-show="!beyond" class="xiangji">
                <van-uploader :after-read="onRead" accept="image/*">
                  <van-icon name="photograph" />
                </van-uploader>
            </li>
        </ul>
    </div>
    <div class="btn" v-on:click="goCircle()">
        发布动态
    </div>
    <dialogCity v-model="haveNoContent" type="fail" title="温馨提示~" :content="showContent"></dialogCity>
  </div>
</template>
<script>
  import headerBar from '../../components/header.vue'
  import footerBar from '../../components/footerBar.vue'
  import dialogCity from '../../components/dialog.vue'
  export default {
    name: 'issue',
    components: {
      footerBar,
      headerBar,
      dialogCity
    },
    data(){
      return {
        textContent: '',
        conterNum: 0,
        picNum: 0,  //图片数量
        upImgUrl: [],   //上传图片的链接
        beyond: false,      //图片是否超出数量
        imgUrl: '',
        iss: true,
        haveNoContent: false,
        showContent: '请输入内容哦~'
      }
    },
    methods: {
      inputText(){
        this.conterNum = this.textContent.length;
      },
      write() {
          let self = this;
          if (self.chnIpt == false) {
              self.conterNum = self.textContent.length;
          }
      },
      importStart() {
          this.chnIpt = true;
      },
      importEnd() {
          this.chnIpt = false;
          this.write();
      },

      onRead(file) {
        let self = this;
        this.upImgUrl.push(file.content);
        this.picNum = this.upImgUrl.length;
        if(self.upImgUrl.length > 8){       //设置大于5时，照相机隐藏
          self.beyond = true;
        }
      },
      closeImg(index){
        let self = this;
        self.upImgUrl.splice(index,1);
        if(self.upImgUrl.length <= 8){
            self.beyond = false;
        }
        self.picNum = self.upImgUrl.length;
      },
      goCircle(){
            if(this.conterNum <= 0){
                this.haveNoContent = true
                return;
            }else{
                    console.log(this.textContent);
                    console.log(this.upImgUrl);
                    let data = {
                        iss: this.iss,
                        con: this.textContent,
                        url: this.upImgUrl
                    }
                    this.textContent = '';
                    this.conterNum = 0;
                    this.upImgUrl = [];
                    this.picNum = 0;
                    this.$emit('listen',data)
            }
      }
    },
  }
</script>
<style lang="less">
.issue{
  .suggestions-item {
    position: relative;
    padding: 0 15px;
    background-color: #ffffff;
    &>h2 {
      font-size: 16px;
      color: #333333;
      font-weight: 500;
      padding-top: 10px;
      & > .label {
          color: #1CB7C6;
          margin-right: 3px;
      }
      & > .little-text {
          font-size: 12px;
          color: #999999;
          margin-left: 5px;
      }
      & >.pic-num {
          font-size: 12px;
          color: #999999;
          float: right;
          margin-right: 15px;
      }
    }
    .type-item {
        display: inline-block;
        width: 160px;
        height: 40px;
        box-sizing: border-box;
        border: 2px solid #bbbbbb;
        line-height: 40px;
        text-align: center;
        border-radius: 23px;
        font-size: 14px;
        color: #333333;
        margin-top: 20px;
        &.type-item-left {
            margin-right: 16px;
        }
        &.thisType {
            color: #ffffff;
            border: none;
            background-color: #1CB7C6;
        }
    }
    #textarea {
        width: 100%;
        height: 160px;
        box-sizing: border-box;
        border-radius: 16px;
        padding: 15px 15px 30px;
        color: #333333;
        font-size: 14px;
        margin: 15px 0 0 2px;
    }
    .counterNum {
        position: absolute;
        bottom: 15px;
        right: 15px;
        font-size: 12px; 
        color: #999999;
    }
    .get-pic {
      width: 100%;
      // height: 75px;
      overflow: hidden;
      padding-top: 18px;
      padding-bottom: 15px;
      // margin-top: 18px;
      &>li {
          float: left;
          width: 50px;
          height: 50px;
          box-sizing: border-box;
          line-height: 50px;
          text-align: center;
          margin-right: 10px;
          margin-bottom: 12px;
          position: relative;
          &.xiangji{
              border: 1px dashed #999999;
          }
          & > img{
              width: 100%;
              height: 100%;
          }
          &:last-child {
              margin: 0;
          }
          i {
              font-size: 30px;
              color: #999999;
              position: relative;
              bottom: -8px;
          }
          .cha{
              position: absolute;
              top: -8px;
              right: -8px;
              width: 15px;
              height: 15px;
              text-align: center;
              font-size: 15px;
              line-height: 14px;
              color: #ffffff;
              border-radius: 10px;
              background-color: rgba(0, 0, 0, 0.7);
          }
          .preview{
              position: fixed;
              top: 0;
              left: 0;
              z-index: 999;
              width: 100%;
              height: 100%;
              background-color: #000;
              img{
                  position: absolute;
                  left: 50%;
                  top: 50%;
                  transform: translate(-50%, -50%);
                  background-size: contain;
                  -webkit-background-size: contain;
              }
          }
      }
    }
   }
   .btn{
        width: 300px;
        height: 40px;
        font-size: 18px;
        background: #efd41e;
        border-radius: 20px;
        text-align: center;
        line-height: 40px;
        margin: 30px auto;
        &:active{
            background: #fff;
        }
    }
}
</style>





