<template>
  <div class="footer">
    <ul class="footBar">
      <li class="footBar-list" v-for="(item,index) in footBarList" @click="goRoute(item,index)" :key="index" :class="{choose : item.isChoose}">
        <span class="bottomIcon"><i class="iconfont" :class="item.con"></i></span>
        <span class="bottomText">{{item.title}}</span>
      </li>
    </ul>
  </div>

</template>
<script>
  export default {
    props: {
      router: {
        type: String
      }
    },
    data () {
      return {
        footBarList: [
          {id: '1', title: '首页', con: 'icon-shouye', turn: 'main', isChoose: true},
          {id: '2', title: '发现', con: 'icon-faxian', turn: 'find', isChoose: false},
          // {id: '3', title: '发布', con: 'icon-iconfontdongtai', turn: 'issue', isChoose: false},
          {id: '4', title: '动态', con: 'icon-iconfontdongtai', turn: 'dynamic', isChoose: false},
          {id: '5', title: '我的', con: 'icon-wode', turn: 'my', isChoose: false}
        ]
      }
    },
    methods: {
      goRoute (item,index) {
        // console.log(this.$router)
        this.$router.push('/' + item.turn)
        // this.footBarList.map(function(obj){
        //   obj.isChoose = false;
        //   console.log(index+1);
        //   console.log(obj.id);
        //   if(index+1 == obj.id){
        //     obj.isChoose = true;
        //   }
        //   return obj;
        // })
      }
    },
    created: function(){
      let self = this;
      this.footBarList.map(function(obj){
          obj.isChoose = false;
          if(self.router == obj.turn){
            obj.isChoose = true;
          }
          return obj;
        })
    }
  }
</script>
<style scoped lang="less">
  *{
    margin: 0;
    padding: 0;
  }
  .choose{
    color: #EFC917;
  }
  html,body{
    width: 100%;
    height: 100%;
  }
  ul,li{
    list-style: none;
  }
  .footer{
    width: 100%;
    box-sizing: border-box;
    position: fixed;
    left: 0;
    bottom: 0;
    background-color: #fff;
    z-index: 999;
    border-top: 1px solid #eee;
    .footBar{
      width: 100%;
      height: 60px;
      display: flex;
      .footBar-list{
        display: inline-block;
        // line-height: 30px;
        height: 100%;
        width: 33.3%;
        text-align: center;
        display: flex;
        flex-direction: column;
        justify-content: center;
        .bottomIcon{
          // display: block;
          width: 100%;
          & > i{
            font-size: 22px;
          }
          // height: 30px;
          // line-height: 30px;
        }
        .bottomText{
            font-size: 14px;
        }
      }
    }
  }
</style>



