<template>
  <div id="my" class="my">
    <div class="head"></div>
    <div class="myContent">
      <div class="avater">
        <van-uploader :after-read="onRead" accept="image/*" class="upImg">
          <!-- <van-icon name="photograph" /> -->
        </van-uploader>
        <img :src="imgUrl" alt="">
      </div>
      <div class="content">
          <div class="topInfo">
            <div>消息 567</div>
            <div>关注 362</div>
            <div>粉丝 198</div>
          </div>
          <!-- <div style="height: 60px;"></div> -->
          <div style="style: both; width: 100%; height: 6px; background: #f1f1f1;"></div>
          <div class="contentList">
            <div class="listItem" v-for="(item, index) in lists" :key="index" v-on:click="goOther(index)">
              <span><i class="iconfont" :style="item.changeColor" :class="item.con"></i></span>
              <span>{{item.text}}</span>
              <span><i class="iconfont icon-dayu"></i></span>
            </div>
          </div>
      </div>
      
    </div>
    <footerBar router="my"/>
  </div>
</template>
<script>
  import footerBar from '../components/footerBar.vue'

  export default {
    name: 'my',
    components: {
      footerBar
    },
    data(){
      return {
        lists: [{
                  con: 'icon-pingjia-copy',
                  text: '我的评价',
                  changeColor: 'color: red;'
                },
                {
                  con: 'icon-bianji',
                  text: '资料编辑',
                  changeColor: 'color: skyblue;'
                },
                {
                  con: 'icon-yaoqing',
                  text: '邀请好友',
                  changeColor: 'color: orange;'
                },
                {
                  con: 'icon-yaoqinghaoyou',
                  text: '我的好友',
                  changeColor: 'color: #efd41e;'
                },
                {
                  con: 'icon-shoucang',
                  text: '我的收藏',
                  changeColor: 'color: #b41eef;'
                },
                {
                  con: 'icon-weibiaoti24',
                  text: '设置',
                  changeColor: 'color: black;'
                },
        ],
        imgUrl: this.$store.state.user.img
      }
    },
    methods:{
      goOther(index){
        let self = this;
        switch(index){
          case 0:
            // self.$router.push();
            break;
          case 1:
            // self.$router.push();
            break;
          case 2:
            // self.$router.push();
            break;
          case 3:
            // self.$router.push();
            break;
          case 4:
            // self.$router.push();
            break;
          case 5:
            self.$router.push('set');
            break;
          default:
            break;
        }
      },
      onRead(file) {
        this.imgUrl = file.content;
        this.$store.commit('setUser', {img: file.content});
      },
    },
    mounted(){
      document.title = '个人中心';
      this.$store.commit('setUser', {name: 'kuke_kuke'})
      console.log(this.$store.state.user.name);
    }
  }
</script>
<style lang="less">
.my{
  width: 100vw;
  height: 100vh;
  overflow-x: hidden;
  position: relative;
  
  .head{
    width: 100%;
    height: 100%;
    background-color: #efd41e;
  }
  .myContent{
    position: absolute;
    left: 50%;
    bottom: 0;
    height: 85%;
    width: 160%;
    transform: translate(-50%);
    background: #ffffff;
    border-top-left-radius: 50%;
    border-top-right-radius: 50%;
    .avater{
      position: absolute;
      left: 50%;
      top: -40px;
      width: 80px;
      height: 80px;
      border-radius: 50%;
      box-sizing: border-box;
      border: 3px solid #ffffff;
      overflow: hidden;
      margin-left: -40px;
      .upImg{
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        border-radius: 50%;
      }
      & > img{
        width: 100%;
        height: 100%;
      }
    }
    .content{
      position: absolute;
      bottom: 0;
      left: 50%;
      transform: translate(-50%);
      width: 62.5%;
      height: 86%;
      margin: 0 auto;
      box-sizing: border-box;
      padding-bottom: 60px;
      overflow-y: scroll;
      // background: pink;
      .topInfo{
        display: flex;
        height: 60px;
        & > div{
          flex: 1;
          line-height: 60px;
          text-align: center;
          color: #999999;
          position: relative;
        }
        & > div:nth-child(1)::after, & > div:nth-child(2)::after{
            content: '';
            position: absolute;
            right: 0;
            top: 50%;
            margin-top: -8px;
            border-right: 1px solid #e1e1e1;
            height: 16px;
        }
      }
      .contentList{
        padding: 0 15px;
        .listItem{
          height: 50px;
          border-bottom: 1px solid #eeeeee;
          display: flex;
          align-items: center;
          &:active{
            background:#f1f1f1;
          }
          & > span:nth-child(1){
            display: inline-block;
            width: 20px;
            height: 20px;
            // background: #999999;
            padding-left: 4px;
            & > i{
              font-size: 18px;
            }
          }
          & > span:nth-child(2){
              flex: 1;
              margin-left: 8px;
          }
          & > span:nth-child(3){
            display: inline-block;
            width: 20px;
            height: 20px;
            // background: #999999;
            & > i{
              color: #999999;
              font-size: 18px;
            }
          }
        }
      }
    }
  }
}

</style>



