<template>
<div class="search">
  <div class="head">
      <span class="back" @click="goBack()"></span>
      <input class="searchText" type="text" placeholder="" v-model="searchInfo" @keyup="search($event)"  @keydown.down="change()" @keydown.up.prevent="up()">
      <span class="searchBtn" @click="directSearch()">搜索</span>
  </div>
  <ul class="searchList">
        <li v-for="(v, i) in myData" :key="i" class="listItem" :class="{bg: i==index}" @click="goSearch(i)">{{v.word}}</li>
    </ul>
</div>
</template>
<script>
export default {
    data(){
        return {
            searchInfo: '',
            myData: [],
            index: -1
        }
    },
    methods:{
        goBack(){
            this.$router.push('main');
        },
        search (e) {
            let self = this;
            // 当按上下键时返回
            if (e.keyCode === 38 || e.keyCode === 40) {
                return
            }
            // 按回车时搜索
            if (e.keyCode === 13) {
                if(self.searchInfo.trim() == ''){
                    return
                }else if(self.index == -1){
                    console.log(self.myData[0].url);
                    window.open(self.myData[0].url);
                    self.searchInfo = ''
                }else{
                    window.open(self.myData[self.index].url);
                }
            }
            // 跨域调用百度搜索接口
            this.$http.get('https://m.ctrip.com/restapi/h5api/searchapp/search?source=mobileweb&action=autocomplete&contentType=json&keyword=' + self.searchInfo, {
                jsonp: 'keyword',    // 必须要有jsonp 后面参数根据实际需要写
            }).then(function (data) {
                console.log('返回数据');
                console.log(data);
                self.myData = data.data.data;
                //          this.myData = data.data.s  // 存储返回的内容
            }, function () {
                console.log('异步请求失败')
            })
        },
         change () {
            console.log('change')
            this.index++
            this.info = this.myData[this.index]  // 输入框显示选择的内容
            if (this.index === this.myData.length) {
            this.index = -1  // 当选择到最后一个时，索引变成-1
            }
        },
        up () {
            console.log('up')
            this.index--
            this.info = this.myData[this.index]
            if (this.index === -1) {
            this.index = this.myData.length - 1
            }
        },
        goSearch(index){
            let self = this;
            window.open(self.myData[index].url);
        },
        directSearch(){
            let self = this;
            if(self.searchInfo.trim() == ''){
                return 
            }else{
                window.open(self.myData[0].url);
            }
        }
    }
}
</script>
<style lang="less" scoped>
.search{
    overflow-x: hidden;
    .head{
        height: 46px;
        width: 100%;
        padding: 0 15px;
        display: flex;
        align-items: center;
        background: rgb(241, 235, 162);
        .back{
            display: inline-block;
            width: 30px;
            height: 30px;
            background-color: #000;
            line-height: 30px;
            margin-right: 6px;
        }
        .searchText{
            flex: 1;
            height: 30px;
            line-height: 30px;
            border: none;
            border-radius: 10px;
            box-sizing: border-box;
            padding-left: 15px;
        }
        .searchBtn{
            display: inline-block;
            width: 60px;
            height: 30px;
            line-height: 30px;
            margin-left: 6px;
            color: #EFC917;
        }
    }
    .searchList{
        width: 100%;
        box-sizing: border-box;
        // padding: 15px;
        // background-color: pink;
        .listItem{
            height: 45px;
            line-height: 45px;
            background-color: #ffffff;
            padding: 0 15px 0 25px;
            margin-bottom: 2px;
            overflow: hidden;
            &.bg{
                background-color: #eeeeee;
            }
        }
    }
}
</style>






