<template>
  <div id="dynamic" class="dynamic">
    <div class="header">
      <!-- <span class="back"></span> -->
      分享的动态
    </div>
    <div class="head">
      <van-tabs :active="active" v-on:click="conIndex">
        <van-tab v-for="(item, index) in titles" :title="item" :key="index">
          <issue v-show="index == 0" v-on:listen="listenMsg"/>
          <circlepage v-show="index == 1" :data="postData"/>
        </van-tab>
      </van-tabs>
    </div>
    <footerBar router="dynamic" v-show="isOriginHei"/>
  </div>
</template>
<script>
  import footerBar from '../components/footerBar.vue'
  import issue from './dynamic/issue.vue'
  import circlepage from './dynamic/circle.vue'
  export default {
    name: 'dynamic',
    components: {
      footerBar,
      issue,
      circlepage
    },
    data(){
      return {
        titles: ['发布动态', '动态空间'],
        isOriginHei: true,
        screenHeight: document.documentElement.clientHeight,        //此处也可能是其他获取方法
        originHeight: document.documentElement.clientHeight,
        active: 0,
        postData: 'woshi'
      }
    },
    methods:{
      listenMsg(data){
        console.log(data);
        if(data.iss){
          this.postData = data;
          this.active = 1;
        }
      },
      conIndex(index){
        if(index == 1){
          this.active = 1;
        }else{
          this.active = 0;
        }
      }
    },
    mounted() {
        let self = this;
        window.onresize = function() {
            return (function(){
                self.screenHeight = document.documentElement.clientHeight;
            })()
        };
        document.title = '发布动态';
    },
    watch: {
        screenHeight (val) {
            if(this.originHeight > val + 100) {        //加100为了兼容华为的返回键
                this.isOriginHei = false;
            }else{
                this.isOriginHei = true;
            }
        }
    }
  }
</script>
<style lang="less">
.dynamic{
  padding-top: 45px;
  .header{
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 46px;
    background-color: #efd41e;
    text-align: center;
    line-height: 46px;
    font-weight: 500;
    font-size: 18px;
    z-index: 999;
  }

  .van-tab--active{
    color: #EFC917;
  }
  .van-tabs__nav-bar{
    background-color: #efd41e;
  }

  .dynamicItem{
    padding: 15px;
    background: #ffffff;
    display: flex;
    justify-content: space-between;
    .left{
      width: 55px;
      padding: 0px 10px 0px 0px;
      box-sizing: border-box;
      position: relative;
      .avater{
        position: absolute;
        top: 0;
        left: 0px;
        width: 42px;
        height: 42px;
        border-radius: 50%;
        background: #efd41e;
        z-index: 2;
      }
      .line{
        width: 3.5px;
        height: 98%;
        background-image: linear-gradient(#888,#fff);
        margin: 2px auto;
        z-index: -1;
      }
    }
    .right{
      width: 300px;
      background: #f1f1f1;
      padding: 15px 10px;
      border-radius: 6px;
      .headTitle{
        margin-bottom: 4px;
        .name{
          display: inline-block;
          font-size: 18px;
          font-weight: 500;
        }
        .date{
          float: right;
          color: #999999;
        }
      }
      .money{
        font-size: 16px;
        color: #999999;
        margin-bottom: 4px;
      }
      .content{
        font-size: 17px;
        margin-bottom: 4px;
      }
      .showImg{
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        & > img{
          width: 84px;
          height: 90px;
          margin-bottom: 10px;
        }
      }
    }
  }
}
</style>



