<template>
  <div class="dialog" :value="value" v-if="visible">
        <div class="alertbg"></div>
        <div class="alertpanel">
            <div class="headimg" v-bind:class="getHeadImg()"></div>
            <div class="title">
                {{title}}
            </div>
            <div class="content" v-html="content"></div>
            <div v-if="type != 'confirm'" class="btns">
                <div v-if="type != 'confirm'" class="secondry-btn alert-btn" v-on:click="okDialog">
                    {{okText}}
                </div>
            </div>
        </div>
        <div v-if="type == 'confirm'" class="btns">
            <div class="secondry-btn cancel-btn" v-on:click="closeDialog(callbackVal)">{{cancelText}}</div>
            <div class="primary-btn confirm-btn" v-on:click="okDialog(callbackVal)">{{okText}}</div>
        </div>
  </div>
</template>
<script>
export default {
    // name: 'abc-dialog',
    props: {
        // default 缺省， success 成功， fail 失败， confirm 确认
        okText: {
            type: String,
            default: '确认'
        },
        cancelText: {
            type: String,
            default: '取消'
        },
        content: {
            type: String,
            default: '内容'
        },
        title: {
            type: String,
            default: '标题'
        },
        type: {
            type: String,
            default: 'default'
        },
        value: {},
        callbackVal: {
            default: null
        }
    },
    data(){
        return {
            visible: false,
        }
    },
    watch: {
        value(val){
            this.visible = val;
        },
        visible(val){
            this.$emit('input', val);
        }
    },
    methods: {
        closeDialog(){
            this.visible = false;
            this.$emit('cancel');
        },
        okDialog(){
            this.visible = false;
            this.$emit('confirm', this.callbackVal);
        },
        getHeadImg(){
            return this.type;
        }
    },
    created: function(){
        console.log(12341234);
        console.log(this.title);
        this.visible = this.value;
    }



}
</script>
<style lang="less" scoped>
     .dialog {
        .alertbg {
            position: fixed;
            width: 100vw;
            height: 100vh;
            top: 0;
            left: 0;
            z-index: 1000;
            opacity: 0.6;
            background: rgba(0, 0, 0, 0.70);
        }
        .alertpanel {
            position: fixed;
            top: 154px;
            padding-bottom: 15px;
            background-image: linear-gradient(-133deg, #ffffff 0%, #ffffff 100%);
            border-radius: 10px;
            width: 315px;
            /*min-height: 212px;*/
            right: 0;
            left: 0;
            margin-right: auto;
            margin-left: auto;
            z-index: 1000;
            .headimg {
                width: 70px;
                height: 70px;
                margin: -35px auto;
                overflow: hidden;
                display: block;
            }
            .headimg.default {
                background: url('../assets/images/face/default.png');
                background-size: 70px 70px;
            }
            .headimg.confirm {
                background: url('../assets/images/face/confirm.png');
                background-size: 70px 70px;
            }
            .headimg.fail {
                background: url('../assets/images/face/fail.png');
                background-size: 70px 70px;
            }
            .headimg.success {
                background: url('../assets/images/face/success.png');
                background-size: 70px 70px;
            }
            .title {
                text-align: center;
                font-size: 16px;
                color: #333333;
                margin-top: 44px;
                letter-spacing: 0;
            }
            .content {
                margin-top: 12px;
                font-size: 15px;
                min-height: 90px;
                color: #666666;
                letter-spacing: 0;
                line-height: 33px;
                text-align: center;
                padding: 0 35px;
                display: flex;
                justify-content: center;
                flex-direction: column;
            }
            .btns {
                /*position: absolute;*/
                /*bottom:15px;*/
                width: 315px;
                margin-top: 15px;
                display: flex;
            }
            .primary-btn {
                margin-left: 15px;
                background-image: linear-gradient(-90deg, #1CB7C6 21%, #1CB7C6 72%);
                width: 135px;
                height: 41px;
                border-radius: 50px;
                text-align: center;
                font-size: 15px;
                color: #ffffff;
                line-height: 41px;
            }
            .primary-btn:active {
                background-image: linear-gradient(-90deg, #22bbcc 21%, #22bbcc 72%);
            }
            .secondry-btn {
                margin-left: 15px;
                border: 1px solid #1CB7C6;
                width: 133px;
                height: 39px;
                border-radius: 50px;
                text-align: center;
                font-size: 15px;
                color: #1CB7C6;
                ;
                line-height: 39px;
            }
            .alert-btn {
                right: 0;
                left: 0;
                margin-right: auto;
                margin-left: auto;
            }
        }
    }
</style>






