<template>
  <div class="allActivity">
        <van-swipe :autoplay="3000" class="swipeContent">
            <van-swipe-item class="change">
                <img src="../../assets/images/findeat/allactivity1.png" alt="" v-on:click="goActivity('guihun')">
            </van-swipe-item>
            <van-swipe-item>
                <img src="../../assets/images/findeat/activity11.png" alt="" v-on:click="goActivity('guihun')">
            </van-swipe-item>
            <van-swipe-item>
                <img src="../../assets/images/findeat/activity22.png" alt="" v-on:click="goActivity('guihun')">
            </van-swipe-item>
            <van-swipe-item>
                <img src="../../assets/images/findeat/activity33.png" alt="" v-on:click="goActivity('guihun')">
            </van-swipe-item>
        </van-swipe>
        <div class="activityItems">
            <div class="item item1" v-on:click="goActivity('guihun')">
                <img src="../../assets/images/findeat/allactivity2.png" alt="">
                <div class="itemText">
                    <p class="title">一起来“鬼混”！有宝藏、有市集、有展览</p>
                    <div class="info">
                        <p class="place">米果滨河部落</p>
                        <p class="place">10月27日-11月28日</p>
                    </div>
                </div>
                <div class="itemTitle">
                    市集
                </div>
            </div>
            <div class="item item2" v-on:click="goActivity('guihun')">
                <img src="../../assets/images/findeat/allactivity3.png" alt="">
                <div class="itemText">
                    <p class="title">各路妖怪魔头齐现身，万圣节不可不玩</p>
                    <div class="info">
                        <p class="place">万塘汇城市生活广场</p>
                        <p class="place">10月27日-10月31日</p>
                    </div>
                </div>
                <div class="itemTitle">
                    市集
                </div>
            </div>
            <div class="item item3" v-on:click="goActivity('guihun')">
                <img src="../../assets/images/findeat/allactivity4.png" alt="">
                <div class="itemText">
                    <p class="title">你和生如夏花，只差一个安迪·沃霍尔的距离</p>
                    <div class="info">
                        <p class="place">杭州大厦501城市广场</p>
                        <p class="place">10月31日</p>
                    </div>
                </div>
                <div class="itemTitle">
                    艺术设计
                </div>
            </div>
            <div class="item item4" v-on:click="goActivity('guihun')">
                <img src="../../assets/images/findeat/allactivity5.png" alt="">
                <div class="itemText">
                    <p class="title">孟京辉戏剧作品《两只狗的生活意见》</p>
                    <div class="info">
                        <p class="place">蜂巢剧院</p>
                        <p class="place">11月04日-11月07日</p>
                    </div>
                </div>
                <div class="itemTitle">
                    歌剧话剧
                </div>
            </div>
            <div class="item item5" v-on:click="goActivity('guihun')">
                <img src="../../assets/images/findeat/allactivity6.png" alt="">
                <div class="itemText">
                    <p class="title">The Color Run 彩色跑杭州站</p>
                    <div class="info">
                        <p class="place">萧山旅游度假区</p>
                        <p class="place">11月06日</p>
                    </div>
                </div>
                <div class="itemTitle">
                    盛世
                </div>
            </div>
            <div class="item item6" v-on:click="goActivity('guihun')">
                <img src="../../assets/images/findeat/allactivity7.png" alt="">
                <div class="itemText">
                    <p class="title">悦览树x人像图书馆【你是我流浪过的一个地方】</p>
                    <div class="info">
                        <p class="place">悦览树Reading Tree Coffee</p>
                        <p class="place">12月04日-01月05日</p>
                    </div>
                </div>
                <div class="itemTitle">
                    摄影
                </div>
            </div>
        </div>
  </div>
</template>
<script>
export default {
    data() {
        return {
            
        }
    },
    methods:{
        goActivity(item){
          switch(item){
            case 'all':
              this.$router.push('allActivity');
              break;
            case 'guihun':
              this.$router.push('activity');
              break;
            case 'article':
              this.$router.push('article');
          }
        }
    },
    mounted(){
        document.title = '全部活动';
    }
}
</script>
<style lang="less" scoped>
.allActivity{
    padding-top: 25px;
    .swipeContent{
        text-align: center;
        img{
            width: 285px;
            height: 125px;
        }
    }
    .activityItems{
        margin-top: 30px;
        padding: 0 15px;
        .item{
            position: relative;
            box-sizing: border-box;
            padding-left: 15px;
            margin-bottom: 20px;
            & > img{
                position: absolute;
                top: 15px;
                left: 0;
                width: 158px;
                height: 120px;
            }
            .itemText{
                width: 100%;
                height: 152px;
                border-radius: 10px;
                background: #f1f1f1;
                box-sizing: border-box;
                padding: 15px 10px 15px 165px;
                display: flex;
                flex-direction: column;
                // justify-content: center;
                .title{
                    flex: 1;
                    font-size: 14px;
                    display: flex;
                    align-items: center;
                }
                .info{
                    flex: 1;
                    font-size: 10px;
                    color: #888888;
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                }
            }
            .itemTitle{
                position: absolute;
                top: 15px;
                left: 10px;
                width: 50px;
                height: 25px;
                line-height: 25px;
                text-align: center;
                color: #efd41e;
                font-size: 10px;
            }
        }
    }
}
</style>



