<template>
<div class="story">
    <div class="storyHead">
        <img src="../../assets/images/story/story1.png">
        <div class="storyText">滨江区由高新开发区、滨江区管理体制整合而成，位于钱塘江北老城区原文教区一带，面积11.44平方公里，是杭州高新区建设发展的发源地，也是高新技术的创新源和中小科技型企业的大孵化器。</div>
        <div class="name">
            <p>滨江</p>
            <p>binjiang</p>
        </div>
    </div>
    <div class="storyTab" :class="{styleFix: changeFix}">
        <div class="tabContent">
            <div class="category" v-on:click="showLeft">品类<i class="iconfont" :class="{'icon-sanjiaoxing-down' : whetherShowLeft,'icon-sanjiaoxing-up': !whetherShowLeft}"></i></div>
            <div class="sort" v-on:click="showRight">排序<i class="iconfont" :class="{'icon-sanjiaoxing-down' : whetherShowRight,'icon-sanjiaoxing-up': !whetherShowRight}"></i></div>
        </div>
        <div class="listContent">
            <div class="categoryListContent" v-show="whetherShowLeft">
                <div class="left">
                    <div v-for="(item, index) in categoryList" v-on:click="chooseCategory(index, item.text)" :class="{active : index == thisIndex}" :key="index"><i class="iconfont" :class="item.icon"></i>{{item.text}}</div>
                </div>
                <div class="right">
                    {{rightContent}}
                </div> 
            </div>
            <div class="sortListContent" v-show="whetherShowRight" v-for="(item, index) in sortList" :key="index" v-on:click="chooseSort(index)" :class="{active : index == thatIndex}"><i class="iconfont" :class="item.icon"></i>{{item.text}}<span class="iconfont icon-duihao" v-show="index == thatIndex"></span></div>
        </div>
    </div>
    <div style="height: 45px;" v-show="stance"></div>
    <div style="height: 15px; background: #efd41e"></div>
    <div class="black">
        <img src="../../assets/images/story/story2.png">
        <h2>非黑即白</h2>
        <div class="titlePic">
            <img src="../../assets/images/story/story3.png">
            <img src="../../assets/images/story/story4.png">
            <img src="../../assets/images/story/story5.png">
        </div>
    </div>
    <div style="height: 15px; background: #efd41e"></div>
    <div class="life">
        <img src="../../assets/images/story/story6.png">
        <h2>谢谢生活</h2>
        <div class="titlePic">
            <img src="../../assets/images/story/story7.png">
            <img src="../../assets/images/story/story8.png">
            <img src="../../assets/images/story/story9.png">
        </div>
    </div>
    <div style="height: 15px; background: #efd41e"></div>
    <div class="sugar">
        <img src="../../assets/images/story/story10.png">
        <h2>糖秋千</h2>
        <div class="titlePic">
            <img src="../../assets/images/story/story11.png">
            <img src="../../assets/images/story/story12.png">
            <img src="../../assets/images/story/story13.png">
        </div>
    </div>
</div>
</template>
<script>
export default{
    data(){
        return {
            categoryList: [
                {text: 'All', icon: ''},
                {text: '吃', icon: 'icon-xiaochi'},
                {text: '玩', icon: 'icon-wanle'},
                {text: '宿', icon: 'icon-zhusu'},
                {text: '买', icon: 'icon-mai'},
                {text: '最近浏览', icon: 'icon-liulanqi'},
            ],
            sortList: [
                {text: '人气最高', icon: 'icon-xin'},
                {text: '离我最近', icon: 'icon-juli'},
                {text: '最新收录', icon: 'icon-shoucang'},
                {text: '评分最高', icon: 'icon-pingfen'},
                {text: '人均最低', icon: 'icon-tubiao03'},
                {text: '人均最高', icon: 'icon-tubiao04'},
            ],
            rightContent: '全部品类',
            thisIndex: 0,
            thatIndex: 0,
            whetherShowLeft: false,
            whetherShowRight: false,
            scrollHeight: '',
            tabHeight: '',
            stance: false,
            changeFix: false
        }
    },
    methods: {
        showLeft(){
            this.whetherShowRight = false;
            this.whetherShowLeft = !this.whetherShowLeft;
        },
        showRight(){
            this.whetherShowLeft = false;
            this.whetherShowRight = !this.whetherShowRight;
        },
        chooseCategory(index, text){
            this.thisIndex = index;
            this.rightContent = text + '页面';
        },
        chooseSort(index){
            this.thatIndex = index;
        },
        menu(){
            this.scrollHeight = document.body.scrollTop || window.scrollY;
            if(this.scrollHeight > this.tabHeight){
                this.stance = true;
                this.changeFix = true;
            }else{
                this.stance = false;
                this.changeFix = false;
            }
        }
    },
    mounted(){
        this.tabHeight = document.getElementsByClassName('storyTab')[0].offsetTop;
        window.addEventListener('scroll', this.menu);
        document.title = '商圈';
    }
}

</script>
<style scoped lang="less">
.story{
    background: #f1f1f1;
    .storyHead{
        background: #eee;
        position: relative;
        & > img{
            width: 100%;
            height: 150px;
        }
        & > .storyText{
            box-sizing: border-box;
            width: 100%;
            padding: 4px 15px;
            font-size: 12px;
        }
        .name{
            position: absolute;
            top: 40%;
            left: 50%;
            transform: translate(-50%, -50%);
            font-size: 26px;
            color: #ffffff;
            & > p{
                text-align: center;
            }
        }
    }
    .storyTab{
        width: 100%;
        position: relative;
        &.styleFix{
            position: fixed;
            top: -2px;
            background: #fff;
        }
        .tabContent{
            width: 100%;
            display: flex;
            margin: 1px 0;
            & > div{
                flex: 1;
                height:45px;
                line-height: 45px;
                text-align: center;
                background: #fff;
                font-size: 14px;
                & > i{
                    margin-left: 2px;
                    font-size: 16px;
                }                
            }
            .category{
                margin-right: 1px;
            }
        }
        .listContent{
            position: absolute;
            width: 100%;
            background: #fff;
            .sortListContent{
                padding: 0 20px;
                height: 45px;
                line-height: 45px;
                font-size: 14px;
                border-bottom: 1px solid #efefef;
                & > i{
                    font-size: 20px;
                    margin-right: 12px;
                }
                & > span{
                    float: right;
                    color: #efd41e;
                    font-size: 22px;
                    margin-right: 30px;
                }
                &.active{
                    & > i{
                        color: #efd41e;
                    }
                }
            }
            .categoryListContent{
                display: flex;
                .left{
                    flex: 2;
                    background: #efefef;
                    & > div{
                        height: 45px;
                        line-height: 45px;
                        font-size: 14px;
                        box-sizing: border-box;
                        padding-left: 55px;
                        text-align: left;
                        position: relative;
                        &.active{
                            color: #efd41e;
                            background: #fff;
                        }
                        & > i{
                            position: absolute;
                            left: 25px;
                        }
                    }
                    
                }
                .right{
                    flex: 3;
                    font-size: 14px;
                    padding: 10px 15px;
                }
            }
        }
    }
    .black, .life, .sugar{
        background: #fff;
        padding-bottom: 14px;
        & > img{
            width: 100%;
            height: 150px;
        }
        & > h2{
            text-align: center;
            line-height: 30px;
        }
        .titlePic{
            display: flex;
            justify-content: space-between;
            padding: 0 18px;
            & > img{
                width: 100px;
                height: 100px;
                border-radius: 12px;
            }
        }
    }
}
</style>


