<template>
  <div class="stay" id="stay" v-on:click="goStory()">
    <div class="stayTittle">
      <div class="top">
        <div>
          <img src="../../assets/images/findeat/stay1.png" alt="">
          <p>酒店</p>
        </div>
        <div>
          <img src="../../assets/images/findeat/stay2.png" alt="">
          <p>旅社</p>
        </div>
        <div>
          <img src="../../assets/images/findeat/stay3.png" alt="">
          <p>民宿</p>
        </div>
        <div>
          <img src="../../assets/images/findeat/stay4.png" alt="">
          <p>别墅</p>
        </div>
      </div>
      <div class="bottom">
        <div>
          <img src="../../assets/images/findeat/stay5.png" alt="">
          <p>客栈</p>
        </div>
        <div>
          <img src="../../assets/images/findeat/stay6.png" alt="">
          <p>露营</p>
        </div>
        <div>
          <img src="../../assets/images/findeat/stay7.png" alt="">
          <p>度假村</p>
        </div>
        <div>
          <img src="../../assets/images/findeat/more.png" alt="">
          <span>···</span>
          <p>更多</p>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
    export default {
      name: 'stay',
      methods:{
        goStory(){
          console.log('story');
          this.$router.push('story');
        }
      }
    }
</script>
<style lang="less">
.stay{
  // padding-bottom: 80px;
  .stayTittle{
    padding: 15px;
    background-color: #fff;
    .top, .bottom{
      margin-top: 10px;
      display: flex;
      justify-content: space-between;
      & > div{
        position: relative;
        & > img{
          width: 75px;
          height: 75px;
        }
        & > span{
          position: absolute;
          top: 0;
          left: 50%;
          transform: translate(-50%);
          font-size: 50px;
          color: #efd41e;
        }
        & > p{
          text-align: center;
        }
      }
    }
  }
}
</style>




