<template>
<div class="ditu">
  <div class="head">
      <!-- <span class="back" @click="goBack()"></span> -->
      <input type="text" class="searchText" ref="searchText" id="searchText" @keyup="keyUpSearch" placeholder="请输入地址"/> 
      <span class="searchBtn" @click="goBack()">返回</span>
  </div>
    <div class="address_items" id="address_result" v-if="searchData.length > 0">  
      <div class="address_item" v-for="item in searchData" :key="item">  
        <div class="title">{{ item.name }}</div>  
        <div class="description">{{ item.pname }}{{ item.cityname }}{{ item.address }}</div>  
      </div>  
    </div>
    <div id="temp"></div>
</div>
</template>
<script>
export default {
  data(){
      return{
        searchData: []
      }
  },
  mounted(){

  },
  methods:{
      keyUpSearch(){
        let self = this;
        var txt = this.$refs.searchText.value;
        AMap.service(["AMap.PlaceSearch"],function(){
          var PlaceSearch = new AMap.PlaceSearch({
            //构造地点查询类
            pageSize: 12,
            pageIndex: 1,
            city: "成都",   //城市
            cityLimit: 'true',
            panel: 'temp'   //搜索结果的展示面板对元素id
          });
          PlaceSearch.search(txt,function(status, result){
            if(status == 'complete' && result.info == 'OK'){
              console.log(result);
            }
          })
        })
      },
      goBack(){
          this.$router.push('main');
      },

  }
}
</script>
<style lang="less" scoped>
.ditu{
  .head{
    height: 46px;
    width: 100%;
    padding: 0 15px;
    display: flex;
    align-items: center;
    background: rgb(241, 235, 162);
    .back{
        display: inline-block;
        width: 30px;
        height: 30px;
        background-color: #000;
        line-height: 30px;
        margin-right: 6px;
    }
    .searchText{
        flex: 1;
        height: 30px;
        line-height: 30px;
        border: none;
        border-radius: 10px;
        box-sizing: border-box;
        padding-left: 15px;
    }
    .searchBtn{
        display: inline-block;
        width: 60px;
        height: 30px;
        line-height: 30px;
        margin-left: 6px;
        color: #EFC917;
    }
  }
}
</style>




