<template>
  <div class="allstory">
      <div class="story-content">
        <div class="content" v-on:click="goSearch('naika')">
          <img src="../../assets/images/store1.png" alt="">
          <span>哈萨奶咖</span>
          <p>COOL到没朋友的美式汉堡</p>
        </div>
        <div class="content" v-on:click="goSearch('minsu')">
          <img src="../../assets/images/store2.png" alt="">
          <span>婳阁民宿</span>
          <p>20多年闺蜜的筑梦空间</p>
        </div>
        <div class="content" v-on:click="goSearch('huyin')">
          <img src="../../assets/images/store3.png" alt="">
          <span>湖隐</span>
          <p>有茶，有酒，只为一场美学盛宴</p>
        </div>
      </div>
    </div>
</template>
<script>
export default {
    methods:{
        goSearch(item){
            switch(item){
                case 'naika':
                    window.location.href = "http://i.meituan.com/s/hangzhou-" + '哈萨奶咖';
                    break; 
                case 'minsu':
                    window.location.href = "http://i.meituan.com/s/hangzhou-" + '婳阁民宿';
                    break;
                case 'huyin':
                    window.location.href = "http://i.meituan.com/s/hangzhou-" + '湖隐';
                    break;
                default:
                    break;
            }
        }
    },
    mounted(){
        document.title = '小店'
    }
}
</script>
<style lang="less" scoped>
.allstory{
    margin-top: 20px;
    .story-content{
      width: 100%;
      box-sizing: border-box;
      padding: 0 12px 0 13px;
      & > .content{
          display: inline-block;
        width: 164px;
        height: 110px;
        margin: 4px;
        & > img{
          width: 100%;
          height: 100%;
        }
        & > span{
          font-size: 14px;
        }
        & > p{
          font-size: 12px;
          color: #666666;
        }
      }
    }
  }
</style>



