<template>
  <div class="otherCity" v-show="showMsg.showOtherCity">
    <p>抱歉，此城市暂无推荐！</p>
    <button class="btn" v-on:click="hideOtherCity">确定</button>
  </div>

</template>
<script>
  export default {
    data () {
      return {
//        showOtherCity: true
      }
    },
    methods: {
      hideOtherCity () {
        this.showMsg.showOtherCity = false
      }
    },
    props: ['showMsg']
  }
</script>
<style scoped="scoped" lang="less">
  .otherCity{
    position: fixed;
    left: 50%;
    top: 50%;
    margin-left: -150px;
    margin-top: -100px;
    width: 300px;
    height: 200px;
    line-height: 200px;
    font-size: 22px;
    text-align: center;
    border: 1px solid #eeeeee;
    background-color: #fff;
    .btn{
      box-sizing: border-box;
      position: absolute;
      margin-left: -75px;
      bottom: 20px;
      outline: none;
      background-color: #F7F7F7;
      width: 150px;
      height: 40px;
      line-height: 40px;
      border-radius: 25px;
      border-color: #eeeeee;

    }
  }
</style>



