<template>
  <div class="introStory">
      <div class="introContent" v-show="article == 'xihu'">
          <h2>西湖</h2>
          <div class="detailContent">
                <p>西湖，位于浙江省杭州市西面，是中国大陆首批国家重点风景名胜区和中国十大风景名胜之一。它是中国大陆主要的观赏性淡水湖泊之一，也是现今《世界遗产名录》中少数几个和中国唯一一个湖泊类文化遗产。</p>
                <div class="videoPlace" v-on:click="playVideo()">
                    <i class="iconfont icon-bofang"></i>
                    <img src="../../assets/images/xihu1.jpg" alt="">
                </div>
                <video src="../../assets/videos/xihu.mp4" id="videoPlay1" v-show="videoShow" class="video" controls>您的浏览器不支持 video 视屏播放。</video>
                <p>西湖三面环山，面积约6.39平方千米，东西宽约2.8千米，南北长约3.2千米，绕湖一周近15千米。湖中被孤山、白堤、苏堤、杨公堤分隔，按面积大小分别为外西湖、西里湖、北里湖、小南湖及岳湖等五片水面，苏堤、白堤越过湖面，小瀛洲、湖心亭、阮公墩三个小岛鼎立于外西湖湖心，夕照山的雷峰塔与宝石山的保俶塔隔湖相映，由此形成了“一山、二塔、三岛、三堤、五湖”的基本格局。</p>
                <p>中山公园原为清朝行宫御花园，1927年，为纪念孙中山先生，公园命名“中山公园”。公园进门迎面石级上书有“孤山”两大字，其中孤字没有一点，人们猜测其意为“孤山不孤”。放鹤亭在孤山东北角，为纪念宋朝以“梅妻鹤子”闻名的林逋而建。浙江省博物馆在孤山南麓，现有各类文物藏</p>
                <p>孤山位于北侧外西湖中，海拔35米，面积约0.22平方公里，为栖霞岭的支脉，也是西湖中最大的岛屿。南宋咸淳《临安志》卷二十三：“一屿耸立，旁无联附，为湖山胜绝处。”今山上林木葱蔚，多历代人文古迹留存。孤山东西分别以白堤和西泠桥与湖岸相连，且岛上名胜古迹甚多，因此杭州人将“孤山不孤”，与“断桥不断”、“长桥不长”并称西湖三怪。</p>
                <p>孤山上主要的景观包括中山公园、浙江省博物馆、文澜阁、西泠印社、放鹤亭、秋瑾墓、俞楼、慕才亭（苏小小墓）等。品10余万件，2004年1月1日起，开始对公众免费开放。文澜阁在浙江省博物馆内西北角，清乾隆年间，为存放《四库全书》，仿北京故宫文渊阁格式改建。西泠印社创办于1904年，因地近西泠桥而命名，是中国近代著名的金石书画艺术团体。岛内还有杭城老字号饭店“楼外楼”，以在西湖活养的草鱼烹制“西湖醋鱼”闻名。</p>
          </div>
      </div>
      <div class="introContent" v-show="article == 'binjiang'">
          <h2>滨江</h2>
          <div class="detailContent">
              <p>滨江区是浙江省杭州市下辖的一个区，位于钱塘江南岸，原属浙江省萧山市，1996年12月12日由原萧山市西兴镇、长河镇、浦沿镇三镇成立，滨江区东面与南面与萧山区相临。</p>
              <div class="videoPlace" v-on:click="playVideo()">
                    <i class="iconfont icon-bofang"></i>
                    <img src="../../assets/images/binjiang.png" alt="">
                </div>
                <video src="../../assets/videos/xihu.mp4" id="videoPlay1" v-show="videoShow" class="video" controls>您的浏览器不支持 video 视屏播放。</video>
              <p>滨江区由高新开发区、滨江区管理体制整合而成。其中，高新区始建于1990年，是国务院批准的首批国家级高新区，位于钱塘江北老城区原文教区一带，面积11.44平方公里，是杭州高新区建设发展的发源地，也是高新技术的创新源和中小科技型企业的大孵化器；滨江区于1996年12月由国务院正式批准设立。开发区管委会与滨江区政府实行两个名称、一套人马。</p>
              <p>2010年滨江区实现生产总值341.7亿元，增长13.1%，技工贸总收入1810亿元，增长18.8%，财政总收入84亿元，增长17%，其中地方财政收入46亿元，增长24.7%；在全国80个国家级高新区中综合排名第八，是浙江省最重要的科技成果产业化基地、技术创新示范基地、创新型人才培养基地和高新技术产品出口基地。</p>
              <p>滨江区境内地势平坦，除回龙山、冠山、紫红岭等少量低山丘陵外，均为钱 塘江泥沙淤积而成的沙土平原。地质属钱塘江冲积平原，地势平坦，地面自然标高为5.2-6.2米（黄海高程），地表以下5-14米范围内为粉砂、粉细砂，地耐力为100-120KPA，可作为工业与民用建筑的天然地基及浅部桩基持力层。大地构造简单，地壳稳定性好，无危害性大的地震等地质灾害发生。</p>
              <p>滨江区属亚热带季风性气候，年平均气温16.27℃，平均相对湿度6%，年平均降雨1452.5mm，年平均日照1899.9小时，年平均蒸发量1235.3mm，年无霜期248天，常年主导风向为东南风。气候温和，日照充足，四季分明，雨水充沛，无冻害等灾害性气候。</p>
          </div>
      </div>
      <div class="introContent" v-show="article == 'xixi'">
          <h2>西溪</h2>
          <div class="detailContent">
              <p>西溪国家湿地公园坐落于浙江省杭州市区西部，离杭州主城区武林门只有6公里，距西湖仅5公里。</p>
              <div class="videoPlace" v-on:click="playVideo()">
                    <i class="iconfont icon-bofang"></i>
                    <img src="../../assets/images/xixi.png" alt="">
                </div>
                <video src="../../assets/videos/xihu.mp4" id="videoPlay1" v-show="videoShow" class="video" controls>您的浏览器不支持 video 视屏播放。</video>
              <p>西溪国家湿地公园总面积约为11.5平方公里，分为东部湿地生态保护培育区、中部湿地生态旅游休闲区和西部湿地生态景观封育区。</p>
              <p>西溪国家湿地公园是一个集城市湿地、农耕湿地、文化湿地于一体的国家湿地公园。2009年11月03日，被列入国际重要湿地名录。2012年1月10日，被评为国家AAAAA级旅游景区。[1]  2017年12月，被评选为全国中小学生研学实践教育基地。</p>
              <p>在四、五千年前，西溪的低湿之地，如受天目山春夏洪水的冲流，此处被淹没便成了湖泊，而干旱之时，湿地也就出现。湿地随隐随现的现象，因此可以把它称为雏形阶段。从东汉熹平元年（172年）建造南湖算起，到唐末五代为止，大约有一千多年时间，是西溪湿地形成期。</p>
              <p>西溪国家湿地公园河流总长100多公里，约70%的面积为河港、池塘、湖漾、沼泽等水域，其间水道如巷、河汊如网，鱼塘栉比、诸岛棋布；陆地绿化率在85%以上，土壤主要有红壤、岩性土和水稻土3个土类，其中以红壤和水稻土分布最为广泛。</p>
          </div>
      </div>
      <div class="introContent" v-show="article == 'dongxin'">
          <h2>东信和创园</h2>
          <div class="detailContent">
              <p>信和创园是仅剩不多的完整保留下来的老空间，历经50多个春秋风雨，当时鼎盛的工业人潮已经从老厂房中褪去，唯有这61幢房子和1000余棵高大的乔木构建着厂区宏大的空间。</p>
              <div class="videoPlace" v-on:click="playVideo()">
                    <i class="iconfont icon-bofang"></i>
                    <img src="../../assets/images/dongxin.png" alt="">
                </div>
                <video src="../../assets/videos/xihu.mp4" id="videoPlay1" v-show="videoShow" class="video" controls>您的浏览器不支持 video 视屏播放。</video>
              <p>2011年，东信·和创园正式开启旧厂房转型文创园区之门。2012年11月18日，梵几客厅成为入驻园区的第一个品牌正式对外经营，东信·和创园的故事就从其所在的15号厂房开始。</p>
              <p>一切因植物而起，初心是想和更多的植物相处，了解它们的习性和喜怒哀乐，和植物一起自然生长。进而开发水泥盆器，外形质朴，透气透水，利于植物生长，也让植物回归到更原始的状态。也会寻找制作各种园艺工具，如园艺剪，镊子，水壶，园艺铲，花洒等，反复使用，对比，找到最适手好用的工具，推荐给更多的人。让我们一起向着光，自然生长。</p>
              <p>听说 2015 年梵几在东信和创园开幕时，这里就只有它一家店。会选择如此偏远的地方，又将展厅称为 “客厅”，这显然是个很有自己个性的品牌。其实第一次接触梵几是在， 当时就喜欢上了他们含蓄温润的风格。虽然杭州店不像北京店有甜点和咖啡（残念），不过空间和家具都美，值得一访。</p>
              <p>梵几家具采用的多是来自北美的白蜡、黑胡桃木，以及国内的核桃木；每件产品从设计到成品都需要 50 天左右的制作周期，经由工匠手工打磨，慢工出细活。朋友最近刚买了一把梵几的折凳，虽然价格颇贵，不过他说摸上去就能感受到上等木头的质感和精细的工艺，非常值得。</p>
          </div>
      </div>
      <div class="introContent" v-show="article == 'nansong'">
          <h2>南宋御街+河坊街</h2>
          <div class="detailContent">
              <p>南宋御街( Southern Song Imperial Street )，是南宋都城临安铺设的一条主要街道。《咸淳临安志》等文献记载，铺设南宋御街一共使用了一万多块石板，但此前的考古发掘却只发现过香糕砖。</p>
              <div class="videoPlace" v-on:click="playVideo()">
                    <i class="iconfont icon-bofang"></i>
                    <img src="../../assets/images/nansong.png" alt="">
                </div>
                <video src="../../assets/videos/xihu.mp4" id="videoPlay1" v-show="videoShow" class="video" controls>您的浏览器不支持 video 视屏播放。</video>
              <p>御街南起皇城北门和宁门（今万松岭和凤凰山路交叉口）外，经朝天门(今鼓楼)、中山中路、中山北路、观桥即今贯桥到今凤起路、武林路交叉口一带，是南宋临安城的中轴线，全长约4185米。它是皇帝于“四孟”（孟春、孟夏、孟秋、孟冬）到景灵宫（今武林路西侧，供奉皇室祖先塑像的场所）朝拜祖宗时的专用道路。</p>
              <p>南宋时期，每三年，皇帝都要进行一次为期三天的祭天仪式。他沿着御街到景灵宫吃斋祭祖，住一晚后，再返回太庙(今鼓楼附近，供奉皇室祖先排位的场所)住一晚，再到城外的郊坛祭天，再住一晚后返回皇宫。据说，皇帝车队走过时因为怕压坏石板，每次都要把石板拿掉，并铺上沙子。</p>
              <p>当时的御街对百姓来说也很重要，因为它两旁集中了数万家商铺，临安城一半的百姓都住在附近。“十里”御街可分三段：首段从万松岭到鼓楼，是临安的政治中心，靠近皇宫、朝廷中枢机关，皇亲国戚、文武百官集中，消费与购买力最强，因此，这里的店铺大多经营金银珍宝等高档奢侈品；第二段从鼓楼到众安桥，以羊坝头，官巷口为中心，是当时的商业中心，经营日常生活用品，据《梦梁录》载，这里名店、老店云集，有名可查的多达120多家；最后一段从众安桥至武林路、凤起路口结束，形成了商贸与文化娱乐相结合的街段，这里有都城最大的娱乐中心——北瓦，日夜表演杂剧、傀儡戏、杂技、影戏、说书等多种戏艺，每天有数千市民在这里游乐休闲。</p>
              <p>2009年十一黄金周，杭州最大的亮点将是新中山路开街[8]  。新中山路最大的亮点，将是鼓楼到西湖大道段的引水入街。昨日下午两点，清灵灵的中河水从凤山闸口一卷而起，从鼓楼开始，沿着中山路曲曲弯弯，一路向北，14个大小不一的水景池一时间被一个个地激活。这是中山路引水工程首次试水，现场专家认为，试水效果已经接近设计要求。</p>
          </div>
      </div>
      <div class="introContent" v-show="article == 'nanshan'">
          <h2>南山路</h2>
          <div class="detailContent">
              <p>南山路，位于杭州市老城区的核心地段，西湖南线风景名胜区的东南面，东靠西湖大道、河坊街、吴山、万松岭，北起解放路与湖滨路相连，南接玉皇山路折而往西，与虎跑、西山路相接，因西湖南部诸山统称南山，路环湖傍山而行，故名。</p>
              <div class="videoPlace" v-on:click="playVideo()">
                    <i class="iconfont icon-bofang"></i>
                    <img src="../../assets/images/nanshan.png" alt="">
                </div>
                <video src="../../assets/videos/xihu.mp4" id="videoPlay1" v-show="videoShow" class="video" controls>您的浏览器不支持 video 视屏播放。</video>
              <p>南山路原长4300余米，原宽20米，清波门以北路段为民国年间拆除古城墙而改建，名涌金路、南山路，后改称膺白路，是为纪念国民党政要黄膺白（郛）而命名，建国后改名南山路；清波门以南路段原为石板土路，是通往净寺进香和西南山区的必经之路。自民国九年（1920），杭城改造湖岸、修建环湖道路后，此路称南屏路，因地处南屏山麓而名。民国二十二年，经铺设柏油、拓宽路面之后，南山路与南屏路便成为市区沟通西湖风景区南线的重要通道，地理位置十分优越。1964年，南山路与南屏路合并，称环湖南路；1981年，复称南山路。西湖南线景区的整合工程于2002年10月完成，改变了西湖过去“北线热南线冷”的旅游格局。现在的南山路北起湖滨一公园，南达长桥公园的南山路以西滨湖地带，湖岸线长3公里。</p>
              <p>南山路地处城、湖结合区域，从历史记载看，此地为吴越、南宋都城以及元末、明、清杭 州古城的西城墙沿线，自南而北，沿湖曾设有钱湖门、清波门、涌金门，再往北通往钱塘门、武林门等地。南宋定都临安（杭州），在凤凰山、万松岭一带设皇宫，将临安府府署迁徙于“清波门之北，以奉国尼寺（即竹园山净因寺）故基创建”。当时，吴山周边和南山路东侧已为政治、经济中心，直至元、明、清和近代，一直都十分繁华。</p>
              <p>柳浪闻莺，经过半个世纪不断的开发和建设，特别是经过近年的西湖综合保护工程，由当年帝王享受的御花园，演变为普通老百姓享用的大乐园，全园分友谊、闻莺、聚景、南园四个景区。其中的友谊园由一片油绿葱翠的草坪和一组造型美观的建筑构成。友谊园的中心矗立着一块“中日不再战”纪念碑，碑高3.1米，用米黄色花岗石铸成，是中日民众在1963年12月共同建立的，反映中日两国希望世代友好的愿望。</p>
              <p>钱王祠占地11300 平方米，建筑面积4600平方米。钱王祠中最恢弘、最有气势的当数正殿——五王殿。它是整个钱王祠当中最重要的地方。这里供奉着吴越国三世五王的塑像。来到殿内，坐在中间的就是钱鏐，钱鏐为吴越国开国国王，称武肃王。两旁是文穆王钱元瓘，忠献王钱弘佐，忠逊王钱弘倧，忠懿王钱弘俶。这三世五王治理杭州86载，定国72年，社会安定，经济发展。北宋统一中原后，又顺应潮流，不动干戈，纳土归宋。</p>
          </div>
      </div>
      <div class="introContent" v-show="article == 'hubin'">
          <h2>湖滨银泰</h2>
          <div class="detailContent">
              <div class="videoPlace" v-on:click="playVideo()">
                    <i class="iconfont icon-bofang"></i>
                    <img src="../../assets/images/hubin.png" alt="">
                </div>
                <video src="../../assets/videos/xihu.mp4" id="videoPlay1" v-show="videoShow" class="video" controls>您的浏览器不支持 video 视屏播放。</video>
              <p>银泰商业（集团）有限公司（简称“银泰商业”）是一家以百货零售、购物中心、电子商务为主营业务的多元化大型商业连锁集团。银泰商业立足中国15年，结合城市特点和市场细分，正在全国加速开发中高端城市综合体，同时，商业模式不断升级创新，从传统百货业到大型购物中心，再到网上购物领域，不断引领着新的时尚生活方式。 2007年，银泰百货成功在香港联交所挂牌上市（01833.HK），2013年3月20日“银泰百货（集团）有限公司”于香港正式更名为“银泰商业（集团）有限公司”。目前银泰已成为国内与奢侈品品牌合作最多的企业之一。15年来，以立足浙江辐射全国为发展轨迹，凭借雄厚的资本、科学的管理、严格的制度、灵活的经营，缔造了一系列骄人的业绩，形成了具有“银泰”特色的三大商业运作模式——“银泰百货”、“银泰购物中心”、“银泰网”。</p>
              <p>杭州湖滨银泰in77位置绝佳，坐落在西湖边上，西湖喷泉音乐广场近在咫尺，是集商业、人文景观、风景旅游为一体的综合性高端商业区。 in77在建筑风格上，保留了江南特有的骑楼、青砖等设计元素，国际品牌旗舰店分布其间，营造出中西文化碰撞的奇妙氛围。 in77目前为4个区，A区集聚国际品牌的旗舰店，B区则致力于打造富有特色的庭院里弄文化商业区，引领全新的青年潮流文化，品牌主要有forever21、i.t、CHOCOOLATE、Aape、STAYREAL及StayReal Café等等；C区为引领新鲜时尚文化的购物中心，以快时尚、流行潮牌、童玩为主，满足年轻时尚欢乐家庭的消费需求；D区作为A区的延续，仍然定位国际名品。此外，亚洲最大的苹果旗舰店也在湖滨银泰in77 B区，是全方位的商业区。</p>
              <p></p>
          </div>
      </div>
  </div>
</template>
<script>
export default {
    data(){
        return{
            msg: this.$route.query.title,        //获取路由传过来的参数
            article: '',
            videoShow: false
        }  
    },
    methods:{
        chooseTitle(){
            let self = this;
            switch(this.msg){
                case 'xihu':
                    self.article = 'xihu';
                    document.title = '西湖';
                    console.log(this.msg);
                    break;
                case 'binjiang':
                    self.article = 'binjiang';
                    document.title = '';
                    break;
                case 'xixi':
                    self.article = 'xixi';
                    document.title = '西溪';
                    break;
                case 'dongxin':
                    self.article = 'dongxin';
                    document.title = '东信和创园';
                    break;
                case 'nansong':
                    self.article = 'nansong';
                    document.title = '南宋御街+河坊街';
                    break;
                case 'nanshan':
                    self.article = 'nanshan';
                    document.title = '南山路';
                    break;
                case 'hubin':
                    self.article = 'hubin';
                    document.title = '湖滨银泰';
                    break;
                default:
                    break;
            }
        },
        playVideo(){
            document.getElementsByClassName('video')[0].play();
        }
    },
    mounted(){
        // document.title = this.msg;
        this.chooseTitle();
    }
}
</script>
<style lang="less" scoped>
.introStory{
    width: 100vw;
    height: 100vh;
    overflow-x: hidden;
    overflow-y: scroll;
    background: #efd41e;
    box-sizing: border-box;
    padding: 15px;
    .introContent{
        background: #ffffff;
        border-radius: 20px;
        padding: 10px 10px 20px;
        & > h2{
            font-weight: 500;
            line-height: 30px;
        }
        .detailContent{
            .videoPlace{
                position: relative;
                width: 80%;
                height: 150px;
                overflow: hidden;
                background: #efd41e;
                border-radius: 10px;
                margin: 0 auto 15px;
                & > img{
                    width: 100%;
                    height: 100%;
                }
                & > i{
                    position: absolute;
                    width: 40px;
                    height: 40px;
                    top: 50%;
                    left: 50%;
                    margin: -20px 0 0 -20px;
                    font-size: 40px;
                    color: #efd41e;
                }
            }
            & > p{
                font-size: 14px;
                line-height: 20px;
                text-indent: 2em;
                margin-bottom: 15px;
            }
        }
    }
}
</style>



