<template>
  <div id="find">
    <headerBar/>
    <div style="height: 46px;"></div>
    <div class="head">
      <van-tabs :active="active">
        <van-tab v-for="(item, index) in category" :title="item" :key="index">
          <eat v-show="index == 0"/>
          <play v-show="index == 1"/>
          <stay v-show="index == 2"/>
        </van-tab>
      </van-tabs>
    </div>
    <div class="findBottom">
      <div class="activity">
      <div class="title-activity" v-on:click="goActivity('all')">精彩活动<i class="iconfont icon-dayu"></i>
      </div>
      <div class="activityShow">
        <div class="left" v-on:click="goActivity('guihun')">
          <img src="../assets/images/findeat/activity_1.png" alt="">
        </div>
        <div class="right">
          <div class="top" v-on:click="goActivity('guihun')">
            <img src="../assets/images/findeat/activity_2.png" alt="">
          </div>
          <div class="bottom" v-on:click="goActivity('guihun')">
            <img src="../assets/images/findeat/activity_3.png" alt="">
          </div>
        </div>
      </div>
      <div class="activity1 activityContent" v-on:click="goActivity('guihun')">
        <img src="../assets/images/findeat/activity11.png" alt="">
        <h3>一起来“鬼”混！有宝藏、有市集、有展览</h3>
        <p>3月5日-4月3日</p>
        <div class="itemTitle">市集</div>
      </div>
      <div class="activity2 activityContent">
        <img src="../assets/images/findeat/activity22.png" alt="">
        <h3>各路妖精魔头齐献身，愚人节不可不玩</h3>
        <p>4月1日-4月8日</p>
        <div class="itemTitle">聚会</div>
      </div>
      <div class="activity3 activityContent">
        <img src="../assets/images/findeat/activity33.png" alt="">
        <h3>你和生如夏花，只差一个安迪·沃霍尔的距离</h3>
        <p>5月1日</p>
        <div class="itemTitle">艺术设计</div>
      </div>
    </div>

    <div class="article">
      <div class="title-article" v-on:click="goActivity('article')">专题文章<i class="iconfont icon-dayu"></i>
      </div>
      <div class="article-content1">
        <div class="content1" v-on:click="goActivity('article')">
          <img src="../assets/images/findeat/article1.png" alt="">
          <!--<span>西湖</span>-->
          <p>等一杯倾心的茶</p>
        </div>
        <div class="content2" v-on:click="goActivity('article')">
          <img src="../assets/images/findeat/article2.png" alt="">
          <p>艺多不压身，一个人的技能满点之路</p>
        </div>
      </div>
      <div class="article-content2">
        <div class="content3" v-on:click="goActivity('article')">
          <img src="../assets/images/findeat/article3.png" alt="">
          <p>第一次来杭州，值得去遇见的小确幸</p>
        </div>
        <div class="content4" v-on:click="goActivity('article')">
          <img src="../assets/images/findeat/article4.png" alt="">
          <p>小酒馆看决赛才是正事</p>
        </div>
      </div>
      <div class="article-content3">
        <div class="content3" v-on:click="goActivity('article')">
          <img src="../assets/images/findeat/article5.png" alt="">
          <p>那些年，我们约好要去逛的店</p>
        </div>
        <div class="content4" v-on:click="goActivity('article')">
          <img src="../assets/images/findeat/article6.png" alt="">
          <p>一个人，在杭州走走停停</p>
        </div>
      </div>
    </div>
    </div>
    
    <footerBar router="find"/>
  </div>

</template>
<script>
  import footerBar from '../components/footerBar.vue'
  import headerBar from '../components/header.vue'
  import eat from './findpages/eat.vue'
  import play from './findpages/play.vue'
  import stay from './findpages/stay.vue'

  export default {
    name: 'find',
    data () {
      return {
        active: 0,
        category: ['食', '玩', '宿']
      }
    },
    components: {
      footerBar,
      headerBar,
      eat,
      play,
      stay
    },
    methods: {
        goActivity(item){
          switch(item){
            case 'all':
              this.$router.push('allActivity');
              break;
            case 'guihun':
              this.$router.push('activity');
              break;
            case 'article':
              this.$router.push('article');
          }
        }
    },
    mounted () {
      document.title = '发现';
    }
  }
</script>
<style lang="less">
  .van-tab--active{
    color: #EFC917;
  }
  .van-tabs__nav-bar{
    background-color: #efd41e;
  }

  .findBottom{
    padding-bottom: 80px;
  }
  .activity{
    background-color: #ffffff;
    margin-top: 10px;
    padding: 10px 15px;
    .title-activity{
      /*text-align: center;*/
      height: 30px;
      line-height: 30px;
      font-size: 16px;
      font-weight: 600;
      & > i{
        float: right;
        width: 20px;
        height: 30px;
        font-size: 20px;
        line-height: 30px;
      }
    }
    .activityShow{
      height: 150px;
      margin-top: 10px;
      .left{
        float: left;
        width: 150px;
        & > img{
          width: 150px;
          height: 150px;
        }
      }
      .right{
        float: right;
        width: 180px;
        .top, .bottom{
          & > img{
            width: 180px;
            height: 70px;
          }
        }
        .bottom{
          margin-top: 6px;
        }
      }
    }
    .activityContent{
      margin-top: 15px;
      position: relative;
      & > img{
        width: 100%;
        height: 125px;
      }
      & > h3{
        font-size: 16px;
        font-weight: 500;
      }
      & > p{
        font-size: 14px;
        color: #bbbbbb;
      }
      .itemTitle{
        position: absolute;
          top: 0;
          left: 14px;
          width: 50px;
          height: 25px;
          line-height: 25px;
          text-align: center;
          color: #efd41e;
          font-size: 10px;
      }
    }
  }

  .article{
    background-color: #ffffff;
    margin-top: 10px;
    padding: 10px 0px;
    .title-article{
      height: 30px;
      line-height: 30px;
      font-size: 16px;
      font-weight: 600;
      padding: 0 15px;
      & > i{
        float: right;
        width: 20px;
        height: 30px;
        font-size: 20px;
        line-height: 30px;
      }
    }
    .article-content1,.article-content2,.article-content3{
      width: 100%;
      box-sizing: border-box;
      display: flex;
      justify-content: space-between;
      padding: 0 10px;
      & > div{
        flex: 1;
        margin: 6px;
        position: relative;
        & > img{
          width: 100%;
        }
        & > span{
          position: absolute;
          left: 8px;
          bottom: 28px;
          padding: 0 2px;
          font-size: 16px;
          color: #ffffff;
          background-color: rgba(0,0,0,0.8);
        }
        & > p{
          font-size: 14px;
          color: #666666;
        }
      }
    }
  }
</style>



