<template>
  <div class="header" id="header">
    <div class="search">
      <span class="search-left" v-on:click="goRoute('dingwei')">
        <i class="iconfont icon-dingwei"></i>
        {{placeText}}
      </span>
      <span class="search-center">推荐</span>
      <span class="search-right" v-on:click="toSearch()">
        <i class="iconfont icon-search"></i>
      </span>
    </div>
  </div>

</template>
<script>
  export default {
    props: {
      router: {
        type: String
      }
    },
    data () {
      return {
        placeText: '杭州'
      }
    },
    methods: {
      goRoute (item) {
        let self = this
        switch (item) {
          case 'dingwei':
            console.log(self.$router)
            self.$router.push('dingwei')
            break
        }
      },
      toSearch(){
        // this.$router.push('search');
        this.$router.push('ditu');
      }
    }
  }
</script>
<style scoped lang="less">
  .search{
    position: fixed;
    width: 100%;
    height: 46px;
    background-color: #efd41e;
    display: flex;
    flex-direction: row;
    padding: 0 0 0 20px;
    z-index: 999;
    & > span {
      line-height: 46px;
      &.search-left{
        width: 70px;
        font-size: 18px;
        & > i{
          font-size: 22px;
          position: relative;
          top: 2px;
        }
      }
      &.search-center{
        flex: 1;
        font-size: 20px;
        text-align: center;
        position: relative;
        left: -5px;
      }
      &.search-right{
        width: 95px;
        box-sizing: border-box;
        text-align: right;
        font-size: 22px;
        padding-right: 40px;
      }
    }
  }
</style>



