import Vue from 'vue'
import Router from 'vue-router'
import main from '../pages/main.vue'
import find from '../pages/find.vue'
import dynamic from '../pages/dynamic.vue'
import my from '../pages/my.vue'
import dingwei from '../pages/dingwei.vue'
import { Tab, Tabs } from 'vant'
import 'vant/lib/vant-css/index.css'
import search from '../components/search.vue'
import ditu from '../pages/mainpages/ditu.vue'
import dialog from '../components/dialog.vue'
import allstory from '../pages/mainpages/allstory.vue'
import allstore from '../pages/mainpages/allstore.vue'
import introStory from '../pages/mainpages/introStory.vue'
import article from '../pages/mainpages/article.vue'
import story from '../pages/findpages/story.vue'
import activity from '../pages/findpages/activity.vue'
import allActivity from '../pages/findpages/allActivity.vue'
// import issue from '../pages/dynamic/issue.vue'
// import circle from '../pages/dynamic/circle.vue'
import set from '../pages/my/set.vue'
import account from '../pages/my/account.vue'

Vue.use(Router)
Vue.use(Tab)
Vue.use(Tabs)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/main',    // 使用rudirect重定向
      component: main
    },
    {
      path: '/main',   // 主页
      name: 'main',
      component: main
    },
    {
      path: '/find',   // 发现
      name: 'find',
      component: find
    },
    // {
    //   path: '/issue',   // 发布
    //   name: 'issue',
    //   component: issue
    // },
    {
      path: '/my',   // 我的
      name: 'my',
      component: my
    },
    {
      path: '/dingwei',   // 定位页面
      name: 'dingwei',
      component: dingwei
    },
    {
      path: '/search',   // 搜索页面
      name: 'search',
      component: search
    },
    {
      path: '/ditu',   // 地图页面
      name: 'ditu',
      component: ditu
    },
    {
      path: '/dialog',   // 地图页面
      name: 'dialog',
      component: dialog
    },
    {
      path: '/story',   // 故事页面
      name: 'story',
      component: story
    },
    {
      path: '/allstory',   // 故事页面
      name: 'allstory',
      component: allstory
    },
    {
      path: '/allstore',   // 小店页面
      name: 'allstore',
      component: allstore
    },
    {
      path: '/introStory',   // 介绍故事页面
      name: 'introStory',
      component: introStory
    },
    {
      path: '/article',   // 文章页面
      name: 'article',
      component: article
    },
    {
      path: '/activity',   // 活动页面
      name: 'activity',
      component: activity
    },
    {
      path: '/allActivity',   // 所有活动页面
      name: 'allActivity',
      component: allActivity
    },
    {
      path: '/dynamic',   // 动态
      name: 'dynamic',
      component: dynamic
    },
    // {
    //   path: '/circle',   // 动态
    //   name: 'circle',
    //   component: circle
    // }
    {
      path: '/set',   // 设置页面
      name: 'set',
      component: set
    },
    {
      path: '/account',   // 账号设置页面
      name: 'account',
      component: account
    },
  ]
})



