<template>
  <div class="activity">
      <div class="bg">
            <div class="activityTitle">
                <div class="titlePic"></div>
                <div class="introction">
                    <h2>一起来鬼混！有宝藏、有集市、有展览！</h2>
                    <p><i class="iconfont icon-juli"></i>米果滨河部落</p>
                    <p><i class="iconfont icon-zhusu"></i>2017-10-27至2017-11-28日 14:00-21:30</p>
                </div>
            </div>
            <div class="activityIntro">
                <div class="introTitle">活动介绍</div>
                <div class="introContent model1">
                    <p>鬼混创意市集</p>
                    <div class="introPic"></div>
                    <p>置身于琳琅满目的创意市集，随处可以遇见充满创意与灵感的新鲜玩意，这是场不能错过的赶集。</p>
                </div>
                <div class="introContent model2">
                    <div class="introPic"></div>
                    <p>鬼混创意展览</p>
                    <p>从智能科技潮品到运动旅行用品，从时尚家居到亲子家庭潮品，还有一群鬼怪穿梭其中，不时驻足欣赏观望。</p>
                </div>
                <div class="introContent model3">
                    <div class="introPic"></div>
                    <p>鬼混接头寻宝</p>
                    <p>恐怖片电影中的场景真实再现，到时候无数的“僵尸”、巫师在接头出现诡异的画风、恐怖的气氛、非常的惊悚，快拉上小伙伴们现出你们的原形。</p>
                </div>
                <div class="introContent model4">
                    <div class="introPic"></div>
                    <p>光怪陆离的排队之上，随时要垂下来的幽灵娃娃，直视就会陷入黑暗的魔法镜子……应有尽有的万圣元素以及魅影重重的布景还在等你现场揭秘！</p>
                </div>
            </div>
            
      </div>
      
  </div>
</template>
<script>
export default {
    mounted(){
        document.title = '鬼混';
    }
}
</script>
<style lang="less" scoped>
*{
    margin: 0;
    padding: 0;
}
.activity{
    width: 100vw;
    height: 100vh;
    .bg{
        background: url('../../assets/images/findeat/activitypage1.png') no-repeat center center;
        background-size: 100% 100%;
        box-sizing: border-box;
        padding: 20px 12px 40px;
        .activityTitle{
            width: 100%;
            text-align: center;
            margin-bottom: 25px;
            & > img{
                width: 327px;
                height: 185px;
                margin: 0 auto;
                margin-bottom: -20px;
            }
            .titlePic{
                width: 340px;
                height: 206px;
                margin: 0 auto;
                background: url('../../assets/images/findeat/activitypage2.png') no-repeat center center;
                background-size: 100% 100%;
            }
            .introction{
                width: 100%;
                box-sizing: border-box;
                background: #ffffff;
                padding: 15px 12px;
                text-align: left;
                margin-top: -20px;
                & > h2{
                    margin-bottom: 15px;
                }
                & > p{
                    font-size: 12px;
                    line-height: 24px;
                    & > i{
                        font-size: 14px;
                        margin-right: 4px;
                    }
                }
            }
        }
        .activityIntro{
            width: 328px;
            background: #ffffff;
            margin: 0 auto;
            .introTitle{
                width: 100px;
                height: 45px;
                margin: 0 auto;
                box-shadow: 0px 3px 5px #f1f1f1;
                font-size: 18px;
                line-height: 45px;
                text-align: center;
            }
            .introContent{
                box-sizing: border-box;
                padding: 0 12px;
                & > p{
                    line-height: 20px;
                    font-size: 14px;
                }
                .introPic{
                    width: 122%;
                    height: 216px;
                    background: url('../../assets/images/findeat/activitypage2.png') no-repeat center center;
                    background-size: 100% 100%;
                    margin-left: -11%;
                }
                &.model2{
                    .introPic{
                        background: url('../../assets/images/findeat/activitypage3.png') no-repeat center center;
                        background-size: 100% 100%;
                        margin-left: -11%;
                    }
                }
                &.model3{
                    .introPic{
                        background: url('../../assets/images/findeat/activitypage4.png') no-repeat center center;
                        background-size: 100% 100%;
                        margin-left: -11%;
                    }
                }
                &.model4{
                    .introPic{
                        background: url('../../assets/images/findeat/activitypage5.png') no-repeat center center;
                        background-size: 100% 100%;
                        margin-left: -11%;
                    }
                }
            }
        }
    }
    
}
</style>



