<template>
  <div class="play" id="play" v-on:click="goStory()">
    <div class="playTittle">
      <div class="top">
        <div>
          <img src="../../assets/images/findeat/play1.png" alt="">
          <p>艺术空间</p>
        </div>
        <div>
          <img src="../../assets/images/findeat/play2.png" alt="">
          <p>杂货铺</p>
        </div>
        <div>
          <img src="../../assets/images/findeat/play3.png" alt="">
          <p>夜蒲</p>
        </div>
        <div>
          <img src="../../assets/images/findeat/play4.png" alt="">
          <p>博物馆</p>
        </div>
      </div>
      <div class="bottom">
        <div>
          <img src="../../assets/images/findeat/play5.png" alt="">
          <p>影像音乐</p>
        </div>
        <div>
          <img src="../../assets/images/findeat/play6.png" alt="">
          <p>书屋</p>
        </div>
        <div>
          <img src="../../assets/images/findeat/play7.png" alt="">
          <p>手作坊</p>
        </div>
        <div>
          <img src="../../assets/images/findeat/more.png" alt="">
          <span>···</span>
          <p>更多</p>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
    export default {
      name: 'play',
      methods:{
        goStory(){
          console.log('story');
          this.$router.push('story');
        }
      }
    }
</script>
<style lang="less">
.play{
  // padding-bottom: 80px;
  .playTittle{
    padding: 15px;
    background-color: #fff;
    .top, .bottom{
      margin-top: 10px;
      display: flex;
      justify-content: space-between;
      & > div{
        position: relative;
        & > img{
          width: 75px;
          height: 75px;
        }
        & > span{
          position: absolute;
          top: 0;
          left: 50%;
          transform: translate(-50%);
          font-size: 50px;
          color: #efd41e;
        }
        & > p{
          text-align: center;
        }
      }
    }
  }
}
</style>





