<template>
  <div class="eat" id="eat" v-on:click="goStory()">
    <div class="eatTittle">
      <div class="top">
        <div>
          <img src="../../assets/images/findeat/coffee.png" alt="">
          <p>咖啡馆</p>
        </div>
        <div>
          <img src="../../assets/images/findeat/hotpot.png" alt="">
          <p>火锅烧烤</p>
        </div>
        <div>
          <img src="../../assets/images/findeat/buffet.png" alt="">
          <p>自助餐</p>
        </div>
        <div>
          <img src="../../assets/images/findeat/western.png" alt="">
          <p>西餐</p>
        </div>
      </div>
      <div class="bottom">
        <div>
          <img src="../../assets/images/findeat/tea.png" alt="">
          <p>茶餐厅</p>
        </div>
        <div>
          <img src="../../assets/images/findeat/sweetmeats.png" alt="">
          <p>甜品铺</p>
        </div>
        <div>
          <img src="../../assets/images/findeat/space.png" alt="">
          <p>茶空间</p>
        </div>
        <div>
          <img src="../../assets/images/findeat/more.png" alt="">
          <span>···</span>
          <p>更多</p>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
    export default {
      name: 'eat',
      methods:{
        goStory(){
          console.log('story');
          this.$router.push('story');
        }
      }
    }
</script>
<style lang="less">
.eat{
  // padding-bottom: 80px;
  .eatTittle{
    padding: 15px;
    background-color: #fff;
    .top, .bottom{
      margin-top: 10px;
      display: flex;
      justify-content: space-between;
      & > div{
        position: relative;
        & > img{
          width: 75px;
          height: 75px;
        }
        & > span{
          position: absolute;
          top: 0;
          left: 50%;
          transform: translate(-50%);
          font-size: 50px;
          color: #efd41e;
        }
        & > p{
          text-align: center;
        }
      }
    }
  }
}
</style>




