<template>
  <div id="circlepage" class="circle">
    <div class="dynamicItem" v-for="(item, index) in $store.state.circle.content" :key="index">
      <div class="left">
          <div class="avater">
            <img src="../../assets/images/avater.jpg" alt="">
          </div>
          <div class="line">
          </div>
      </div>
      <div class="right">
        <div class="headTitle">
          <div class="name">Rookie</div>
          <div class="date">10月26日</div>
        </div>
        <div class="money">CNY 60/人</div>
        <div class="content">
          {{item.con}}
        </div>
        <div class="showImg">
          <img :src="it" alt="" v-for="(it, ind) in item.url" :key="ind">
          <!-- <img src="../../assets/images/findeat/activity_1.png" alt="">
          <img src="../../assets/images/findeat/activity_2.png" alt="">
          <img src="../../assets/images/findeat/activity_2.png" alt="">
          <img src="../../assets/images/findeat/activity_2.png" alt="">
          <img src="../../assets/images/findeat/activity_2.png" alt="">
          <img src="../../assets/images/findeat/activity_1.png" alt="">
          <img src="../../assets/images/findeat/activity_2.png" alt="">
          <img src="../../assets/images/findeat/activity_2.png" alt=""> -->
        </div>
        <div class="comment">
          此处是点赞评价
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
        props: ['data'],
        name: 'circlepage', 
        data(){
            return {
                circleList: [
                    // {
                    //     con: '内容1',
                    //     url: ['../../assets/images/findeat/activity_2.png']
                    // },
                    // {
                    //     con: '内容2',
                    //     url: ['../../assets/images/findeat/activity_2.png']
                    // }
                ]
            }
        },
        watch: {
            data(newVal,oldeVal){
                console.log(newVal)
                this.circleList.unshift(newVal);
                this.$store.commit('setCircle', {content: this.circleList})
                console.log(this.$store.state.circle.content);
                
                // this.$store.commit('setUser', {name: 'kuke_kuke'})
                // console.log(this.$store.state.user.name);
            }
        },

        mounted(){
            
        }
  }
</script>
<style lang="less">
.circle{
  .van-tab--active{
    color: #EFC917;
  }
  .van-tabs__nav-bar{
    background-color: #efd41e;
  }
  .dynamicItem{
    padding: 15px;
    background: #ffffff;
    display: flex;
    justify-content: space-between;
    .left{
      width: 55px;
      padding: 0px 10px 0px 0px;
      box-sizing: border-box;
      position: relative;
      .avater{
        position: absolute;
        top: 0;
        left: 0px;
        width: 42px;
        height: 42px;
        border-radius: 50%;
        background: #efd41e;
        z-index: 2;
        overflow: hidden;
        & > img{
          width: 100%;
          height: 100%;
        }
      }
      .line{
        width: 3.5px;
        height: 98%;
        background-image: linear-gradient(#888,#fff);
        margin: 2px auto;
        z-index: -1;
      }
    }
    .right{
      width: 300px;
      background: #f1f1f1;
      padding: 15px 10px;
      border-radius: 6px;
      .headTitle{
        margin-bottom: 4px;
        .name{
          display: inline-block;
          font-size: 18px;
          font-weight: 500;
        }
        .date{
          float: right;
          color: #999999;
        }
      }
      .money{
        font-size: 16px;
        color: #999999;
        margin-bottom: 4px;
      }
      .content{
        font-size: 17px;
        margin-bottom: 4px;
      }
      .showImg{
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        & > img{
            display: inline-block;
          width: 84px;
          height: 90px;
          margin-bottom: 10px;
        }
      }
    }
  }
}
</style>



