<template>
  <div class="main">
    <headerBar/>
    <div class="banner">
      <ul class="banner-list">
        <li v-on:click="goSearch('jiuba')">
          <img src="../assets/images/banner1.png" alt="">
        </li>
        <li v-on:click="goRoute('xihu')">
          <img src="../assets/images/xihu1.jpg" alt="">
        </li>
        <li v-on:click="goSearch('meishi')">
          <img src="../assets/images/banner3.png" alt="">
        </li>
        <li v-on:click="goRoute('nanshan')">
          <img src="../assets/images/banner4.png" alt="">
        </li>
        <li v-on:click="goSearch('jiuba')">
          <img src="../assets/images/banner1.png" alt="">
        </li>
      </ul>
      <ul class="order-list">
        <li></li>
        <li></li>
        <li></li>
        <li></li>
      </ul>
    </div>

    <div class="story">
      <div class="title-story" v-on:click="goRoute('story')">故事<i class="iconfont icon-dayu"></i>
      </div>
      <p class="title-decription">陪你捕捉城市的惊喜</p>
      <div class="story-content1">
        <div class="content1" v-on:click="goRoute('xihu')">
          <img src="../assets/images/xihu.png" alt="">
          <span>西湖</span>
          <p>醉在江南烟雨时</p>
        </div>
        <div class="content2" v-on:click="goRoute('binjiang')">
          <img src="../assets/images/binjiang.png" alt="">
          <span>滨江</span>
          <p>钱塘江南岸的高新区</p>
        </div>
      </div>
      <div class="story-content2">
        <div class="content3" v-on:click="goRoute('xixi')">
          <img src="../assets/images/xixi.png" alt="">
          <span>西溪</span>
          <p>西溪秀色如玉</p>
        </div>
        <div class="content4" v-on:click="goRoute('dongxin')">
          <img src="../assets/images/dongxin.png" alt="">
          <span>东信和创园</span>
          <p>老厂房里的创意空间</p>
        </div>
      </div>
    </div>
    <div class="littleStore">
      <div class="title-store" v-on:click="goRoute('store')">悠悠小店<i class="iconfont icon-dayu"></i></div>
      <p class="title-decription">去了解 从清晨到黑夜</p>
      <div class="store store1" v-on:click="goSearch('naika')">
        <img src="../assets/images/store1.png" alt="">
        <p>COOL到没朋友的美式汉堡</p>
        <p>LONDON STAION · 上城区</p>
        <span>餐厅</span>
      </div>
      <div class="store store2" v-on:click="goSearch('minsu')">
        <img src="../assets/images/store2.png" alt="">
        <p>20多年闺蜜的筑梦空间</p>
        <p>婳阁民宿 · 西湖区</p>
        <span>旅社</span>
      </div>
      <div class="store store3" v-on:click="goSearch('huyin')">
        <img src="../assets/images/store3.png" alt="">
        <p>有茶，有酒，只为一场美学盛宴</p>
        <p>湖隐 · 西湖区</p>
        <span>茶空间</span>
      </div>
    </div>
    <div class="guess">
      <div class="title-guess">猜你喜欢</div>
      <p class="title-decription">不试试 怎么知道呢</p>
      <div class="guess-content1">
        <div class="content1" v-on:click="goRoute('article')">
          <img src="../assets/images/like1.png" alt="">
          <p>生于土地，隐于自然</p>
        </div>
        <div class="content2">
          <img src="../assets/images/like2.png" alt="">
          <p>在一个安心的地方做做木头</p>
        </div>
      </div>
      <div class="guess-content2">
        <div class="content3">
          <img src="../assets/images/like3.png" alt="">
          <p>米其林三星厨师的佳作</p>
        </div>
        <div class="content4">
          <img src="../assets/images/like4.png" alt="">
          <p>精酿的口感是“活”的</p>
        </div>
      </div>
      <div class="guess-content3">
        <div class="content5">
          <img src="../assets/images/like5.png" alt="">
          <p>中式美学的践行者</p>
        </div>
        <div class="content6">
          <img src="../assets/images/like6.png" alt="">
          <p>与音乐为伍</p>
        </div>
      </div>
    </div>
    <footerBar router="main"/>
  </div>
</template>

<script>
  import footerBar from '../components/footerBar.vue'
  import headerBar from '../components/header.vue'
  export default {
    components: {
      footerBar,
      headerBar
    },
    data () {
      return {
        placeText: '杭州'
      }
    },
    methods: {
      test () {
        let self = this
        console.log(self.$route)
      },
      goRoute (item) {
        let self = this
        switch (item) {
          case 'dingwei':
            self.$router.push('dingwei')
            break;
          case 'story':
            self.$router.push('allstory');
            break;
          case 'xihu':
            self.$router.push('introStory?title=xihu');
            break;
          case 'binjiang':
            self.$router.push('introStory?title=binjiang');
            break;
          case 'xixi':
            self.$router.push('introStory?title=xixi');
            break;
          case 'dongxin':
            self.$router.push('introStory?title=dongxin');
            break;
          case 'store':
            self.$router.push('allstore');
            break;
          case 'article':
            self.$router.push('article');
            break;
        }
      },
      goSearch(item){
          switch(item){
            case 'naika':
                window.location.href = "http://i.meituan.com/s/hangzhou-" + '哈萨奶咖';
                break; 
            case 'minsu':
                window.location.href = "http://i.meituan.com/s/hangzhou-" + '婳阁民宿';
                break;
            case 'huyin':
                window.location.href = "http://i.meituan.com/s/hangzhou-" + '湖隐';
                break;
            case 'jiuba':
                window.location.href = "http://i.meituan.com/s/hangzhou-" + '流年酒吧';
                break; 
            case 'meishi':
                window.location.href = "https://meishi.meituan.com/i/poi/162748406?ct_poi=076454623224023786424878446497000910439_e3115639137902748782_a%25e6%258d%259e%25e7%258e%258b&cevent=imt%2Fpoilist%2Fitem";
                break; 
            default:
                break;
          }
      }
    },
    mounted () {
      this.test();
      document.title = 'ucity首页';
    }
  }
</script>
<style lang="less" scoped>
  .main{
    /*overflow-y: scroll;*/
    padding-bottom: 70px;
  }
  // .search{
  //   height: 55px;
  //   background-color: #ffffff;
  //   display: flex;
  //   flex-direction: row;
  //   padding: 0 0 0 20px;
  //   & > span {
  //     line-height: 55px;
  //     &.search-left{
  //       width: 70px;
  //       font-size: 18px;
  //       & > i{
  //         font-size: 22px;
  //         position: relative;
  //         top: 2px;
  //       }
  //     }
  //     &.search-center{
  //       flex: 1;
  //       font-size: 20px;
  //       text-align: center;
  //       position: relative;
  //       left: -5px;
  //     }
  //     &.search-right{
  //       width: 75px;
  //       box-sizing: border-box;
  //       text-align: right;
  //       font-size: 22px;
  //       padding-right: 25px;
  //     }
  //   }
  // }
  .banner{
    height: 188px;
    width: 100%;
    overflow: hidden;
    position: relative;
    padding-top: 46px;
    & > ul.banner-list{
      position: absolute;
      left: 0;
      top: 46px;
      width: 500%;
      height: 188px;
      animation: bannerMove 20s ease-out infinite;
      & > li{
        float: left;
        width: 20%;
        img{
          width: 100%;
          height: 188px;
        }
      }
    }
    & > ul.order-list{
      position: absolute;
      left: 50%;
      bottom: 20px;
      width: 60px;
      height: 10px;
      /*background-color: rgba(255,255,255,0.6);*/
      margin-left: -30px;
      & > li{
        float: left;
        width: 10px;
        height: 10px;
        /*background-color: #efd41e;*/
        background-color: rgba(255,255,255,0.6);
        border-radius: 50%;
        margin-right: 4px;
        &:nth-child(1){
          animation: orderMove 20s linear infinite;
        }
        &:nth-child(2){
          animation: orderMove 20s 5s linear infinite;
        }
        &:nth-child(3){
          animation: orderMove 20s 10s linear infinite;
        }
        &:nth-child(4){
          animation: orderMove 20s 15s linear infinite;
        }
        &.active{
          background-color: #efd41e;
        }
      }
    }
  }
  @keyframes bannerMove {
    0%,10%{
      transform: translateX(0);
    }
    25%,35%{
      transform: translateX(-20%);
    }
    50%,65%{
      transform: translateX(-40%);
    }
    75%,85%{
      transform: translateX(-60%);
    }
    100%{
      transform: translateX(-80%);
    }
  }
  @keyframes orderMove {
    0%,35%{
      background-color: #efd41e;
    }
    35%,100%{
      background-color: rgba(255,255,255,0.6);
    }
  }
  .story{
    margin-top: 20px;
    .title-story{
      text-align: center;
      font-size: 16px;
      font-weight: 600;
      & > i{
        float: right;
        width: 30px;
        height: 30px;
        font-size: 24px;
        line-height: 30px;
        margin-right: 20px;
      }
    }
    .title-decription{
      color: #666666;
      font-size: 14px;
      text-align: center;
      margin: 6px;
    }
    .story-content1,.story-content2{
      width: 100%;
      box-sizing: border-box;
      padding: 0 4px;
      display: flex;
      & > div{
        flex: 1;
        margin: 4px;
        position: relative;
        & > img{
          width: 100%;
        }
        & > span{
          position: absolute;
          left: 8px;
          bottom: 28px;
          padding: 0 2px;
          font-size: 16px;
          color: #ffffff;
          background-color: rgba(0,0,0,0.8);
        }
        & > p{
          font-size: 14px;
          color: #666666;
        }
      }
    }
  }
  .littleStore{
    margin-top: 20px;
    padding: 0 10px;
    .title-store{
      text-align: center;
      font-size: 16px;
      font-weight: 600;
      & > i{
        float: right;
        width: 30px;
        height: 30px;
        font-size: 24px;
        line-height: 30px;
        margin-right: 20px;
      }
    }
    .title-decription{
      color: #666666;
      font-size: 14px;
      text-align: center;
      margin: 6px;
    }
    .store{
      width: 100%;
      border: 1px dashed #e1e1e1;
      border-radius: 10px;
      padding-bottom: 15px;
      margin-bottom: 20px;
      position: relative;
      & > img{
        width: 100%;
      }
      & > p{
        padding-left: 5px;
        &:nth-of-type(2){
          color: #666666;
          font-size: 14px;
          margin-top: 4px;
        }
      }
      & > span{
        position: absolute;
        right: 10px;
        top: 10px;
        padding: 0 2px;
        font-size: 14px;
        color: #ffffff;
        background-color: rgba(0,0,0,0.8);
      }
    }
  }

  .guess{
    margin-top: 20px;
    .title-guess{
      text-align: center;
      font-size: 16px;
      font-weight: 600;
    }
    .title-decription{
      color: #666666;
      font-size: 14px;
      text-align: center;
      margin: 6px;
    }
    .guess-content1,.guess-content2,.guess-content3{
      width: 100%;
      box-sizing: border-box;
      padding: 0 4px;
      display: flex;
      & > div{
        flex: 1;
        margin: 4px;
        position: relative;
        & > img{
          width: 100%;
        }
        & > p{
          font-size: 14px;
        }
      }
    }
  }
</style>




